import { Router } from './router';
import { RouterEvents } from './types';
export class RouterProvider extends HTMLElement {
    constructor() {
        super(...arguments);
        this.onRouterRequest = (e) => {
            e.detail.router = this.router;
        };
    }
    connectedCallback() {
        document.body.addEventListener(RouterEvents.Request, this.onRouterRequest, true);
        if (!this.router) {
            this.router = new Router({
                interceptLocals: this.hasAttribute('intercept-locals'),
                useHash: this.hasAttribute('use-hash')
            });
        }
        Promise.resolve().then(this.router.resolve);
    }
    disconnectedCallback() {
        document.body.removeEventListener(RouterEvents.Request, this.onRouterRequest, true);
    }
}
