import { StoreEvents, } from './types';
export function normalizeEvent(fn) {
    return (event, data) => {
        if (typeof event === 'string')
            event = { type: event, data };
        return fn(event);
    };
}
export function requestStore(el) {
    const event = new CustomEvent(StoreEvents.Request, { detail: {} });
    el.dispatchEvent(event);
    return event.detail.store;
}
export function createUpdateEvent(state, mutation) {
    return new CustomEvent(StoreEvents.Update, {
        detail: {
            state,
            mutation,
        },
    });
}
export function createDispatchEvent(type, data) {
    return new CustomEvent(StoreEvents.Dispatch, {
        detail: {
            type,
            data,
        },
    });
}
